import React, { Fragment } from "react";
import { Hotel, Bucket } from "./types";
import { Slider } from "./Slider";
import { Tooltip, Checkbox, NumberRange } from "@blueprintjs/core";
import { DateRangeInput, DateRange } from "@blueprintjs/datetime";
import { stringToDate, hotelIsWithinDistance } from "./utils";
import "./Filters.scss";

interface Props {
  bucketedHotels: Bucket<Hotel>[];
  onChangeDateRange: (newRange: DateRange) => void;
  onChangeDistanceRange: (newRange: NumberRange) => void;
  onChangeStayLengthRange: (newRange: NumberRange) => void;
  onChangeHotelSelection: (hotel: Hotel) => void;

  selectedDistanceRange: NumberRange;
  maxDistance: number;

  selectedStayLengthRange: NumberRange;
  maxStayLength: number;

  dateRange: DateRange;
}

export const Filters: React.FC<Props> = (props: Props) => {
  return (
    <div className="filters">
      <h3>Filters</h3>
      <p>Distance from Georgetown</p>
      <Slider
        min={0}
        max={props.maxDistance}
        value={props.selectedDistanceRange}
        onRelease={props.onChangeDistanceRange}
        labelStepSize={5}
        labelSuffix="mi"
      />
      <p className="stay-length">Stay Length</p>
      <Slider
        value={props.selectedStayLengthRange}
        min={1}
        max={props.maxStayLength}
        stepSize={1}
        labelStepSize={2}
        labelSuffix=" days"
        onRelease={props.onChangeStayLengthRange}
      />
      <div className="date-range-container">
        <DateRangeInput
          className="date-range"
          shortcuts={false}
          formatDate={date => date.toDateString()}
          parseDate={stringToDate}
          minDate={props.dateRange[0]}
          maxDate={props.dateRange[1]}
          onChange={props.onChangeDateRange}
        />
      </div>
      <div className="hotels">
        {props.bucketedHotels.map(bucket => (
          <Fragment key={`${bucket.ceiling}`}>
            <h4>{`Under ${bucket.ceiling} ${
              bucket.ceiling === 1 ? "mile" : "miles"
            } away`}</h4>
            {bucket.items.map(hotel => {
              const outOfRange = !hotelIsWithinDistance(
                hotel,
                props.selectedDistanceRange
              );
              return (
                <div key={hotel.name}>
                  <Tooltip
                    content="Hotel is not in the selected distance range"
                    disabled={!outOfRange}
                  >
                    <Checkbox
                      checked={hotel.selected && !outOfRange}
                      disabled={outOfRange}
                      onChange={() => props.onChangeHotelSelection(hotel)}
                      key={hotel.name}
                      label={hotel.name}
                    />
                  </Tooltip>
                </div>
              );
            })}
          </Fragment>
        ))}
      </div>
    </div>
  );
};
