import React, { useState, useEffect } from "react";
import "./App.css";
import { MarriottMatrix } from "./MarriottMatrix";
import prices from "./prices.json";
import { ScrapedPricePoint, PricePoint } from "./types";
import moment from "moment";

function useWindowWidth() {
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  });

  return width;
}

function processScrapedPricePoint(pricePoint: ScrapedPricePoint): PricePoint {
  const [checkIn, checkOut] = pricePoint.dates;

  // TODO: url generation should be on backend
  let bookingLink: string | undefined = undefined;
  if (pricePoint.booking_link) {
    const propertyCodeMatch = pricePoint.booking_link.match(
      /propertyCode=([A-Z]+)&/
    );
    if (propertyCodeMatch === null) {
      throw Error(
        "No property code match in booking link: " + pricePoint.booking_link
      );
    }
    const propertyCode = propertyCodeMatch[1];

    bookingLink =
      "https://www.marriott.com/reservation/availabilitySearch.mi?&propertyCode=" +
      propertyCode +
      "&fromDate=" +
      moment(checkIn).format("MM/DD/YYYY") +
      "&toDate=" +
      moment(checkOut).format("MM/DD/YYYY") +
      "&numberOfRooms=1&numberOfGuests=1&currency=USD";

    if (pricePoint.corporate_rate) {
      bookingLink += "&clusterCode=corp&corporateCode=P5R";
    }
  }

  return {
    hotelName: pricePoint.name,
    stayDates: { checkIn, checkOut },
    corporateRate: pricePoint.corporate_rate,
    pricePerNight: pricePoint.price_per_night,
    soldOut: pricePoint.sold_out,
    distance: pricePoint.distance,
    bookingLink
  };
}

const App: React.FC = () => {
  const width = useWindowWidth();
  if (width < 1000) {
    return <p className="too-small">Desktop only :-)</p>;
  }
  return <MarriottMatrix pricePoints={prices.map(processScrapedPricePoint)} />;
};

export default App;
