import moment from "moment";
import { PricePoint, Hotel, PriceGridData, StayDates } from "./types";
import { NumberRange } from "@blueprintjs/core";
import { DateRange } from "@blueprintjs/datetime";

export function formatDate(date: string) {
  return {
    dayOfWeek: moment(date).format("ddd"),
    date: moment(date).format("M/D")
  };
}

export const DEFAULT_HOTELS: string[] = [
  // "The Ritz-Carlton Georgetown, Washington, D.C.",
  // "The Westin Georgetown, Washington D.C.",
  // "Washington Marriott Georgetown",
  // "The Ritz-Carlton, Washington, DC",
  // "The Mayflower Hotel, Autograph Collection",
  // "The Westin Georgetown, Washington D.C."
];

export function datesToKey(stay: StayDates): string {
  return `${stay.checkIn}-${stay.checkOut}`;
}

export function getLast<T>(array: T[]): T {
  return array[array.length - 1];
}

export function getStayLength(stay: StayDates) {
  return moment(stay.checkOut).diff(moment(stay.checkIn), "days");
}

export function pricePointIsWithinStayLengthRange(
  stay: StayDates,
  stayLengthRange: NumberRange
) {
  const length = getStayLength(stay);
  return length >= stayLengthRange[0] && length <= stayLengthRange[1];
}

export function hotelIsWithinDistance(
  hotel: Hotel,
  distanceRange: NumberRange
) {
  return (
    hotel.distance >= distanceRange[0] && hotel.distance <= distanceRange[1]
  );
}

export function datesAreChronological(first?: Date, second?: Date): boolean {
  if (!first || !second) {
    return true;
  }
  return moment(second).diff(moment(first)) >= 0;
}

export function dateIsInRange(dateStr: string, range: DateRange) {
  const date = stringToDate(dateStr);
  return (
    datesAreChronological(range[0], date) &&
    datesAreChronological(date, range[1])
  );
}

export function getPriceFromGrid(
  grid: PriceGridData | undefined,
  stay: StayDates
) {
  if (grid) {
    const key = datesToKey(stay);
    const pricePoint = grid.map.get(key);
    return pricePoint && pricePoint.pricePerNight;
  }
}

export class PricePointMap<V extends PricePoint> extends Map<string, V[]> {
  public addPricePoint(key: string, pricePoint: V) {
    if (this.get(key) === undefined) {
      super.set(key, []);
    }
    const list = super.get(key);
    if (list === undefined) {
      throw Error(`No list for key: ${key} in PricePointMap`);
    }
    list.push(pricePoint);
    return this;
  }

  public set(): this {
    throw Error(
      "Calling set() in a PricePointMap is disallowed. Use addPricePoint instead."
    );
  }
}

export function stringToDate(date: string) {
  return moment(date).toDate();
}
