import React from "react";
import classNames from "classnames";
import "./CheckOutRow.scss";
import { formatDate } from "./utils";

interface Props {
  checkInDates: string[];
  checkOut?: string;
  activeCheckIn?: string;
  getPrice?: (checkOut: string) => number | undefined;
  shouldTint?: (checkIn: string) => boolean;
  onClick?: (checkIn: string) => void;
  getPricePercentile?: (checkIn: string) => number | undefined;
  onHover?: (checkIn: string) => void;
  offHover?: (checkIn: string) => void;
}

export const CheckOutRow: React.FC<Props> = (props: Props) => {
  return (
    <tr>
      {props.checkInDates.map(checkIn => {
        if (props.getPrice && props.checkOut) {
          const price = props.getPrice && props.getPrice(checkIn);
          return (
            <td
              key={checkIn}
              onClick={onClickCell(checkIn)}
              onMouseEnter={() => props.onHover && props.onHover(checkIn)}
              onMouseLeave={() => props.offHover && props.offHover(checkIn)}
              className={classNames(
                {
                  clickable: price !== undefined,
                  active: props.activeCheckIn === checkIn,
                  tint: props.shouldTint && props.shouldTint(checkIn)
                },
                checkIn < props.checkOut ? getColorClass(checkIn) : "impossible"
              )}
            >
              {price !== undefined && <p>{`$${price}`}</p>}
            </td>
          );
        } else {
          return (
            <th
              key={checkIn}
              className={classNames(
                "checkin-date",
                props.shouldTint && props.shouldTint(checkIn) && "tint"
              )}
            >
              {renderDate(checkIn)}
            </th>
          );
        }
      })}
      {props.checkOut && <th>{renderDate(props.checkOut)}</th>}
    </tr>
  );

  function renderDate(dateStr: string) {
    const { dayOfWeek, date } = formatDate(dateStr);
    return (
      <>
        <p>{dayOfWeek}</p>
        <p>{date}</p>
      </>
    );
  }

  function onClickCell(checkIn: string) {
    return () => props.onClick && props.onClick(checkIn);
  }

  function getColorClass(checkIn: string) {
    if (!props.getPricePercentile) {
      return;
    }
    const percentile = props.getPricePercentile(checkIn);
    if (percentile === undefined) {
      return "no-price";
    }
    if (percentile >= 0.9) {
      return "cost-1";
    } else if (percentile > 0.7) {
      return "cost-2";
    } else if (percentile > 0.3) {
      return "cost-3";
    } else if (percentile > 0.1) {
      return "cost-4";
    } else {
      return "cost-5";
    }
  }
};
