import React, { useState } from "react";
import { NumberRange, RangeSlider, IRangeSliderProps } from "@blueprintjs/core";
import "./Slider.scss";

interface Props extends IRangeSliderProps {
  value: NumberRange;
  labelSuffix?: string;
}

export const Slider: React.FC<Props> = (props: Props) => {
  const [localValue, setLocalValue] = useState<NumberRange>(props.value);
  const { value, labelSuffix, ...spreadableProps } = props;
  return (
    <div>
      <RangeSlider
        className="slider"
        labelRenderer={label => (
          <span className="no-wrap">
            {label.toLocaleString() + (labelSuffix || "")}
          </span>
        )}
        labelStepSize={1}
        stepSize={0.1}
        {...spreadableProps}
        value={localValue}
        onChange={onChange}
        onRelease={onRelease}
      />
    </div>
  );

  function onRelease(value: NumberRange) {
    setLocalValue(value);
    props.onRelease && props.onRelease(value);
  }

  function onChange(value: NumberRange) {
    setLocalValue(value);
    props.onChange && props.onChange(value);
  }
};
